.empty{
    background: #0b0b0b;
    height: 100vh;
    color: #fcca15;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    img{
        width: 400px;
        margin: auto;
    }
    button{
        background: #fcca15;
        color: black;
        height: 40px;
        font-size: 20px;
        font-weight: 400;
        width: 100%;
    }
}
.cart{
    background: #0b0b0b;
    color: #fcca15;
    padding: 100px 60px;
    width: 100%;

    .display{
        margin-bottom: 20px;
        width: 74%;
        margin-left: 60px;
        display: flex;
        justify-content: space-between;
    }
    .cart-Items{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px 60px;
        margin: 20px 0px;
        width: 100%;
        border: 2px solid #fcca15;
        border-radius: 10px;
        .name{
            width: 100px;
            text-align: center;
        }
        img{
            width: 200px;
            height: 170px;
            border-radius: 10px;
        }
        .buttonss{
            background: #fcca15;
            color: #0b0b0b;
            border-radius: 10px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100px;
            height: 35px;
            .minus{
                background: #fcca15;
                border: 1px solid #fcca15;
                width: 35px;
                height: 35px;
                border-top-left-radius: 10px;
                border-bottom-left-radius: 10px;
            }
            .plus{
                border: 1px solid #fcca15;
                background: #fcca15;
                width: 35px;
                height: 35px;
                border-top-right-radius: 10px;
                border-bottom-right-radius: 10px; 
            }
        }
        .trash{
            background: #0b0b0b;
            border: 1px solid #0b0b0b;
            color: #fcca15;
        }
    }
    .confirm{
        background: #fcca15;
        color: #0b0b0b;
        margin: 0 auto;
        text-align: center;
        padding: 10px;
        width: 40%;
        border: 2px solid #fcca15;
        border-radius: 10px;
        .count{
            display: flex;
            justify-content: space-around;
        }
        .inputs{
            display: block;
            input{
                width: 300px;
                margin: 10px 0px;
                background: #0b0b0b;
                padding: 10px;
                border: none;
                border-radius: 10px;
                color: #fcca15;
            }
        }
        .confirmBtn{
            width: 300px;
            height: 50px;
            background: black;
            border: none;
            border-radius: 10px;
            color: #fcca15;
            transition: 0.5s ease;
            margin: 10px 0px;
        }
        .confirmBtn:hover{
            opacity: 0.8;
        }
    }
}